<template>
  <v-container style="background-color: #384055">
    <v-layout row wrap>
      <v-row no-gutters>
        <v-col cols="4">
          <v-card
            tile
            class="ma-4 pa-4"
            style="background-color: #2f3546"
            height="900"
          >
            <v-row class="mt-1">
              <v-col cols="3">
                <div style="color: #ffffff">Setting ▾</div></v-col
              >
              <v-spacer></v-spacer>

              <v-col cols="3"
                ><v-select
                  :items="['SectionA', 'SectionB']"
                  outlined
                  dense
                  v-bin
                  v-bind:class="{ disabled: true }"
                  placeholder="Section"
                >
                </v-select
              ></v-col>
            </v-row>
            <v-divider style="background-color: #ffffff"></v-divider>
            <v-row class="mt-7">
              <v-col class="a">Calibration</v-col>
              <v-col
                ><v-btn
                  @click="calibrationMod"
                  depressed
                  outlined
                  dense
                  class="fb-btn"
                  >Setting</v-btn
                ></v-col
              >
            </v-row>
            <v-row>
              <v-col class="a">Zoning</v-col>
              <v-col
                ><v-btn
                  @click="areaSettingMod"
                  depressed
                  outlined
                  dense
                  class="fb-btn"
                  >Setting</v-btn
                ></v-col
              >
            </v-row>
            <v-row class="mt-10">
              <v-col cols="4" class="a">Monitoring Schedule</v-col>
              <v-col>
                <v-row>
                  <v-col cols="3" class="a">From :</v-col>

                  <v-col
                    ><vue-timepicker
                      format="HH:mm"
                      v-model="check_start_time"
                      
                    >
                    </vue-timepicker
                  ></v-col>
                </v-row>

                <v-row>
                  <v-col cols="3" class="a">To :</v-col>
                  <v-col class="a"
                    ><vue-timepicker
                      format="HH:mm"
                      v-model="check_end_time"
                    >
                    </vue-timepicker
                  ></v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6" class="a">Detection Type</v-col>
              <v-col cols="3"
                ><v-select
                  :items="['People', 'Car']"
                  label="People"
                  outlined
                  dense
                   v-bind:class="{ disabled: true }"
                ></v-select
              >
              

              </v-col>
            </v-row>
            <v-row>
              <v-col cols="3" class="a">Alarm Type</v-col>
            </v-row>

            <v-row class="ma-0 pa-0" no-gutters>
              <v-col cols="6">
                <v-switch
                  class="a"
                  inset
                  v-model="check_specker"
                  color="blue"
                  true-value="true"
                  false-value="false"
                  hide-details
                >
                <template v-slot:label>
                  <span class="input__label">Speaker</span>
                </template>
                </v-switch>
              </v-col>

              <v-col cols="6">
                <v-text-field 
                  v-model="check_message"
                  outlined
                  
                >
                <template v-slot:label>
                  <span class="input__label">Type Alarming Message</span>
                </template>
                </v-text-field>
              </v-col>
            </v-row>

            <v-row no-gutters class="ma-0 pa-0">
              <v-col cols="6">
                <v-switch
                  class="a"
                  inset
                  v-model="check_telbot"
                  color="blue"
                  true-value="true"
                  false-value="false"
                  hide-details
                >
                 <template v-slot:label>
                  <span class="input__label">Message</span>
                </template>
                </v-switch>
              </v-col>

              <v-col cols="6">
                <v-text-field  outlined>
                  <template v-slot:label>
                  <span class="input__label">Type Phone Number</span>
                </template>
                </v-text-field>
              </v-col>
            </v-row>

            <v-row no-gutters class="ma-0 pa-0">
              <v-col cols="6">
                <v-switch
                  class="a"
                  inset
                  v-model="check_email"
                  color="blue"
                  true-value="true"
                  false-value="false"
                  hide-details
                >
                <template v-slot:label>
                  <span class="input__label">E-mail</span>
                </template>
                </v-switch>
              </v-col>

              <v-col cols="6">
                <v-text-field 
                outlined
                  v-model="check_email_addr">
                  <template v-slot:label>
                  <span class="input__label">Type E-mail Adress</span>
                  </template>
                </v-text-field>
              </v-col>
            </v-row>

            <v-row class="mt-10" ailgn="center" justify="center">
              <v-btn class="btn2" @click="saveSection" label="Label"
                >Start Monitoring</v-btn
              >
            </v-row>
          </v-card>
        </v-col>
        <v-col cols="8">
          <v-card
            tile
            class="ma-4 pa-4"
            style="background-color: #2f3546"
            height="900"
          >
            <v-row>
              <v-col cols="3">
                <v-select
                  v-model="selectSetting"
                  :items="['Calibration', 'Zoning Setting']"
                  label="Calibration Setting"
                  return-object
                ></v-select>
              </v-col>
              <v-spacer></v-spacer>
              <v-col cols="2">
                <v-select
                  v-if="selectSetting === 'Calibration'"
                  v-model="check_selected_camera"
                  @change="changeCamera"
                  :items="camUrl"
                  item-text="name"
                  item-value="value"
                  return-object
                ></v-select
              ></v-col>
            </v-row>
            <v-divider style="background-color: #ffffff"></v-divider>
            <v-row>
              <calibration
                class="mt-15"
                v-if="selectSetting === 'Calibration'"
                ref="calibration"
                :selectedCamera="check_selected_camera"
              ></calibration>
              <draw-area v-else ref="drawarea" />
            </v-row>
            <v-row class="mt-5">
              <v-spacer></v-spacer>
              <v-btn class="btn2 mr-5" v-if="selectSetting === 'Calibration'" @click="resetData"
                >Reset</v-btn
              >
              <v-btn class="btn2 mr-5" v-else @click="resetArea">Reset</v-btn>
              <v-btn class="btn2 mr-5" @click="confirm">Confirm</v-btn>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import DrawArea from "./DrawArea.vue";
import Calibration from "./Calibration.vue";
import VueTimepicker from "vue2-timepicker/src/vue-timepicker.vue";
import toggleVue from "./toggle.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: { Calibration, DrawArea, VueTimepicker, toggleVue },
  name: "Setting",

  data() {
    return {
      selectSetting: "Calibration",
      selectedCamera: { name: "Cam1", value: "1OFFICE1.jpg" },
      areaShow: false,
      camID: ["Cam1", "Cam2", "Cam3", "Cam4"],
      selectCam:1,
      useEmail:null,
      useSpeaker:null,
      useTelBot:null,
      detectionType : "p",
      mapImagePath : "/media/testmap.png",
      email : "hyeonjin@paimedialab.com",
      alarmMessage : "test1",

      camUrl: [
        { name: "Cam1", value: "1OFFICE1.jpg" },
        { name: "Cam2", value: "1OFFICE2.jpg" },
        { name: "Cam3", value: "1OFFICE3.jpg" },
        { name: "Cam4", value: "1OFFICE4.jpg" },
      ],

      startTimeValue: {
        HH: "00",
        mm: "00",
      },

      endTimeValue: {
        HH: "00",
        mm: "00",
      },


    };
  },

  created: {
  },

  mounted () {
    this.getSectionInfo()
    this.changeCamera({
      "name":"Cam1",
      "value":"1OFFICE1.jpg"
    })
  },

  computed: {
    ...mapGetters(["GET_SECTION_INFO"]),

    check_email:{
      get(){
       return this.$store.getters.GET_USE_EMAIL
       },
      set(val){
        console.log(val)
        this.$store.commit("SET_USE_EMAIL", val);
      }
      },

    check_telbot:{
      get(){return this.$store.getters.GET_USE_TELBOT},
      set(val){this.$store.commit("SET_USE_TELBOT", val);}
    },

    check_specker:{
      get(){return this.$store.getters.GET_USE_SPECKER},
      set(val){this.$store.commit("SET_USE_SPECKER", val);}
    },
    // check_specker(){return this.$store.getters.GET_USE_SPECKER},

    check_email_addr(){return this.$store.getters.GET_EMAIL_ADDRESS},

    check_message:{
      get(){return this.$store.getters.GET_ALARM_MESSAGE},
      set(val){
        this.$store.commit("SET_MESSAGE", val);
      }
    },

    check_start_time:{
      get(){
        return this.$store.getters.GET_ST
      },
      set(val){
        this.$store.commit("SET_START_TIME", val);
      }
      
    },

    check_end_time:{
      get(){
        return this.$store.getters.GET_ET
      },
      set(val){
        console.log("asdasd", val)
        this.$store.commit("SET_END_TIME", val);
      }
    },

    check_cam_id:{
      get(){
       return this.$store.getters.GET_CAMID
       },
      set(val){
        console.log(val)
        this.$store.commit("SET_CAMID", val);
      }
      },

    check_selected_camera:{
      get(){
       return this.$store.getters.GET_SELECTEC_CAM
       },
      set(val){
        console.log(val)
        this.$store.commit("SET_SELECTEC_CAM", val);
      }
      },
  },
  watch: {
      check_telbot(val){
        this.useTelBot = val
      },
      check_email(val){
        this.useEmail = val
      },
      check_specker(val){
        this.useSpeaker = val
      },
      check_email_addr(val){
        this.email = val
      },
      check_message(val){
        this.alarmMessage = val
      },

      check_start_time(val){
        this.startTimeValue = val
      },

      check_end_time(val){
        this.endTimeValue = val
      },

      check_cam_id(val){
        this.selectCam = val
      },

      check_selected_camera(val){
        this.selectedCamera = val
      }

  },

  methods: {
    ...mapActions([
      "FETCH_SECTION_INFO",
      "POST_SECTION_INFO",
    ]),

    getSectionInfo() {
      this.$store.dispatch("FETCH_SECTION_INFO");

    },
    calibrationMod() {
      this.selectSetting = "Calibration";
    },

    areaSettingMod() {
      this.selectSetting = "Zoning Setting";
    },

    modChage(e) {
      this.areaShow = e;
    },

    resetData() {
      this.$refs.calibration.clearPoints();
      console.log("Reset Data.");
    },
    resetArea() {
      this.$refs.drawarea.clearPoints2();
    },

    changeCamera(event) {
      console.log("EVENT:", event)
      var num = parseInt(event.name.slice(-1))
      this.selectCam = num
      this.resetData();
      this.$refs.calibration.getCamInfo(this.selectCam);
    },

    confirm(){
      this.$refs.calibration.local_confirm();
    },

    saveSection() {
      this.$store.dispatch("POST_SECTION_INFO");
      this.$router.push("/Monitoring")
      // this.$store.dispatch("FETCH_SECTION_INFO");
    },


  },

  props: {
    msg: String,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fb-btn.v-btn--outlined {
  border: thin solid #2984ff;
  color: #ffffff;
}

.btn2 {
  background-color: #2984ff;
  background: #2984ff;
  color: #ffffff;
}

.a {
  color: #ffffff;
}

.input__label {
   color: white;
}


.a2 {
  font: 5px;
  color: #ffffff;
}

.theme--light.v-btn:not(.v-btn--flat):not(.v-btn--text):not(.v-btn--outlined) {
  background-color: #2984ff;
}
.v-select__selection,
.v-select__selection--comma,
.v-select.v-text-field input {
  color: rgb(255, 255, 255) !important;
}

.picker {
  color: #ffffff;
}

.disabled {
  pointer-events: none;
  border-color: #ffffff;
  /* color: #ffffff;
  cursor: not-allowed;
  background-image: none;
  background-color: #0a58df;
  border-color: #ffffff;    */
}

.v-input--switch .v-input--selection-controls__input {
  margin-right: 10px;
}
.v-input--switch__track {
  width: 45px;
  height: 26px;
  top: calc(50% - 13px);
  left: 0;
  right: 0;
  border-radius: 13px;
}
.v-input--switch__thumb {
  box-shadow: none;
  transform: translateX(4px);
}

.v-input--switch.v-input--is-dirty .v-input--selection-controls__ripple,
.v-input--switch.v-input--is-dirty .v-input--switch__thumb {
  transform: translateX(22px);
}

.v-input--switch .v-input--selection-controls__ripple {
  left: -12px;
}

/* ----------- */




</style>
