import axios from 'axios';
import constants from "@/constants";

async function AreaDataInfo() {
    try {
        var data = JSON.stringify({"section_name":"IFA"});
        var config = {
            method: 'get',
            url: `${constants.Domain}/api/area/returnAreaInfo/`,
            headers: { 
              'Content-Type': 'application/json'
            },
            data : data
          };
        return await axios(config);
        //return await axios.get(`${constants.Domain}api/area/returnAreaInfo/`);
    } catch (error) {
        console.log(error);
    } 
}


