async function postSectionInfo(d) {
    try {
        var data = JSON.stringify({
            "section_name":"IFA",
            "monitoring_start_time":d.monitoring_start_time.toString(),
            "monitoring_end_time":d.monitoring_end_time.toString(),
            "alarm_message":d.alarm_message,
            "detect_object_type":"P",
            "use_speaker_flag": JSON.parse(d.use_speaker_flag),
            "use_email_flag":JSON.parse(d.use_email_flag),
            "use_telbot_flag":JSON.parse(d.use_telbot_flag)
        });
        //var data = JSON.stringify({"section_name":"IFA","monitoring_start_time":"1000","monitoring_end_time":"1700","alarm_message":"test","detect_object_type":"P","use_speaker_flag":true,"use_email_flag":false,"use_telbot_flag":true});
        var config = {
          method: 'post',
          url: `${constants.Domain}/api/section/saveSectionData/`,
          headers: { 
            'Content-Type': 'application/json'
          },
          data : data
        };

        return await axios(config);
        //return await axios.get(`${constants.Domain}api/area/returnAreaInfo/`);
    } catch (error) {
        console.log(error);
    } 
}

