<template>
  <v-container class="mt-5" style="background-color: #2f3546" fill-height>
    <v-layout row wrap align-center justify-center>
      <v-card
        tile
        class="ma-4 pa-4"
        style="background-color: #384055"
        height="620"
        width="500"
        align="center"
        justify="center"
      >
        <v-row>
          <v-col cols="2">
            <div class="zone">Zoning Map</div>
          </v-col>
          <v-col cols="9">
            <template>
              <div class="ct mt-4">
                <v-stage
                  ref="stage"
                  :config="stageSize"
                  @mousemove="handleMouseMove"
                  @mouseDown="handleMouseDown"
                  @mouseUp="handleMouseUp"
                >
                  <v-layer ref="layer">
                    <v-image
                      :config="{
                        x: 0,
                        y: 0,
                        width: stageSize.width,
                        height: stageSize.height,
                        image: image,
                      }"
                    />
                    <v-rect
                      v-for="(rec, index) in check_items"
                      :key="index"
                      :config="{
                        x: Math.min(
                          rec.startPointX,
                          rec.startPointX + rec.width
                        ),
                        y: Math.min(
                          rec.startPointY,
                          rec.startPointY + rec.height
                        ),
                        width: Math.abs(rec.width),
                        height: Math.abs(rec.height),
                        fill: rec.color,
                        stroke: rec.color,
                        strokeWidth: 3,
                        dash: [10, 10],
                      }"
                    />
                  </v-layer>
                </v-stage>
              </div>
            </template>
          </v-col>
        </v-row>
      </v-card>
      <v-card
        tile
        class="ma-4 pa-4"
        style="background-color: #2f3546"
        height="620"
        width="500"
        align="center"
        justify="center"
      >
        <v-btn width="465" depressed outlined dense class="fb-btn mb-5" @click="addArea"
          >+ Add New Area</v-btn
        >
        <v-list style="background-color: #384055">
          <v-list-item v-for="(it, index) in check_items" :key="it.areaName">
            <v-menu
              v-model="it.menu"
              top
              nudge-bottom="105"
              nudge-left="16"
              :close-on-content-click="false"
            >
              <template v-slot:activator="{ on }">
                <!-- <div class="" :style="swatchStyle" v-on="on" /> -->
                <div class="" v-on="on">
                  <v-btn :style="{ background: it.color }"></v-btn>
                </div>
              </template>
              <v-card>
                <v-card-text class="pa-0">
                  <v-color-picker v-model="it.color" flat />
                </v-card-text>
              </v-card>
            </v-menu>
            <v-text-field class="mx-3" v-model="it.areaName"></v-text-field>
            <v-btn width="100" depressed outlined dense class="fb-btn2 mx-4" @click="drawOn(it.areaName, index)">select area</v-btn>
            <v-btn @click="deleteArea(index)" depressed outlined dense class="fb-btn2 mx-4 mx-2">Delete</v-btn>

            <!-- <v-btn class="mx-2" >...</v-btn> -->
          </v-list-item>
        </v-list>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
const width = window.innerWidth;
const height = window.innerHeight;

export default {
  name: "Area",

  data() {
    return {
      stageSize: {
        width: 960,
        height: 540,
      },
      image: null,
      isDrawing: true,
      points: [],
      lines: [],
      isDrawing: false,
      DrawStart: false,
      recs: [],
      items: [],
      // items: [{ areaName: "RestrictArea", color: "#000000", menu: false }],
      item: {
        id: 1,
        areaIndex: 0,
        areaName: "RestrictArea",
        areaColor: "#000000",
        area_x1: 50,
        area_y1: 50,
        area_x2: 100,
        area_y2: 100,
        check_delay: 0,
        created_at: "2023-08-09T04:53:44.787600Z",
        sectionName: 1,
      },

      color: "#1976D2FF",
      colors: [],
      menu: false,
      menu2: false,
      selectedIndex : 99,
    };
  },
  mounted() {
    const container = document.querySelector(".ct");
    const observer = new ResizeObserver(() => {
      this.stageSize.width = container.offsetWidth;
      this.stageSize.height = container.offsetHeight;
    });
    observer.observe(container);
    const image = new window.Image();
    image.src = "http://192.168.33.19/media/testmap.png";
    image.onload = () => {
      this.image = image;
    };

    this.getAreaInfo();
  },

  computed: {
    swatchStyle() {
      const { color, menu } = this;
      return {
        backgroundColor: color,
        cursor: "pointer",
        height: "30px",
        width: "30px",
        transition: "border-radius 200ms ease-in-out",
        borderRadius: menu ? "50%" : "4px",
      };
    },

    check_items: {
      get() {
        return this.$store.getters.GET_ITEMS;
      },
      set(val) {
        console.log("check_items:", val);
        this.$store.commit("SET_ITEMS", val);
      },
    },

    check_rects: {
      get() {
        return this.$store.getters.GET_RECTS;
      },
      set(val) {
        console.log(val);
        this.$store.commit("SET_RECTS", val);
      },
    },
  },

  watch: {
    check_items(val) {
      this.items = val;
    },
    check_rects(val) {
      this.recs = val;
    },
  },

  methods: {
    deleteArea(index){
      console.log("delete Index:", index)
      // this.items = this.items.slice(index)
      // this.recs = this.recs.slice(index)
      // this.items.pop(index)
      this.items.splice(index, 1);
      this.recs.splice(index, 1);
      // this.recs.pop(index)
      // console.log("delete Index:", a)
    },

    getAreaInfo() {
      this.$store.dispatch("FETCH_AREA_INFO");
    },

    handleMouseDown(event) {
      if (this.DrawStart) {
        this.isDrawing = true;
        const pos = this.$refs.stage.getNode().getPointerPosition();
        console.log("AreaPos:", pos);
        this.setRecs([
          ...this.recs,
          { startPointX: pos.x, startPointY: pos.y, width: 0, height: 0 },
        ]);

        this.items[this.selectedIndex].startPointX = pos.x;
        this.items[this.selectedIndex].startPointY = pos.y;
        this.items[this.selectedIndex].width = 0;
        this.items[this.selectedIndex].height = 0;
        
      }
    },

    handleMouseUp() {
      this.isDrawing = false;
      this.DrawStart = false;
    },

    setRecs(element) {
      this.recs = element;
    },

    handleMouseMove(event) {
      // no drawing - skipping
      if (!this.isDrawing) {
        return;
      }
      
      const point = this.$refs.stage.getNode().getPointerPosition();
      console.log(point.x, point.y)

      this.items[this.selectedIndex].width =  point.x - this.items[this.selectedIndex].startPointX
      this.items[this.selectedIndex].height = point.y - this.items[this.selectedIndex].startPointY

      // let curRec = this.recs[this.recs.length - 1];
      // curRec.width = point.x - curRec.startPointX;
      // curRec.height = point.y - curRec.startPointY;
    },

    clearPoints2() {
      console.log("AreaReset.");
      this.recs = [];
      this.items = [];
      this.getAreaInfo();
    },

    addArea() {
      var data = {
        areaName: "Areaname",
        color: "#00000030",
        menu: false,
        menu2: false,
        startPointX : 0,
        startPointY : 100,
        width : 100,
        height : 100,
      };
      this.items.push(data);
    },

    drawOn(name, index) {
      //       this.items.pop(index)
      // this.recs.pop(index)
      console.log(name, index);
      console.log(this.items[index])
      // this.recs.pop(index)
      this.DrawStart = true;
      this.selectedIndex = index;
    },
  },
};
</script>
<style>
.zone {
  color: #cecece;
  font-size: 9px;
}

.colorpicker {
  background-color: color;
  cursor: "pointer";
  height: "30px";
  width: "30px";
  transition: "border-radius 200ms ease-in-out";
}

.fb-btn.v-btn--outlined {
  border: thin solid #2984ff;
  color: #ffffff;
}

.fb-btn2.v-btn--outlined {
  border: thin solid #ffffff;
  color: #ffffff;
  font-size: 12px;
}
</style>
