    async workingAreaData() {
      const token ="cnmJjxQvDJBOAHl5Q6UW_GlBQmMRTD5baVAOyoZb67mpLqMtG5chA94KimPPtTnqiVOZk0LGin-fkJuktTThbQ==";
      const url = "http://211.55.84.163:7984";

      const startHour = 8; // 고정된 시작 시간: 8am
      const endHour = 20; // 고정된 종료 시간: 8pm
      const intervalHours = 2; // 시간 간격: 2시간

      const currentDate = new Date();
      console.log("currentDate = ",currentDate)
      const year = currentDate.getFullYear();
      const month = (currentDate.getMonth() + 1).toString().padStart(2, "0");
      const day = currentDate.getDate().toString().padStart(2, "0");
      const formattedDate = `${year}-${month}-${day}`;

      const params = { org: "paimedialab" };

      const extractedData = [];

      console.log("formattedDate = ",formattedDate) 


      for (let hour = startHour; hour <= endHour - intervalHours;hour += intervalHours) {
        
        const parts = this.selectDate.split("/");
        const formattedString = `${parts[2]}-${parts[0]}-${parts[1]}`;
        console.log("formattedString 222 = ", formattedString)

        

        const formattedStartTime = `${formattedString}T${hour.toString().padStart(2, "0")}:00:00Z`;
        const formattedEndTime = `${formattedString}T${(hour + intervalHours).toString().padStart(2, "0")}:00:00Z`


        const query = `
          from(bucket: "localization")
          |> range(start: ${formattedStartTime}, stop: ${formattedEndTime})
          |> filter(fn: (r) => r["_measurement"] == "localization")
          |> filter(fn: (r) => r["Ort"] == "IFA")
          |> filter(fn: (r) => r["_field"] == "LOCATION" or r["_field"] == "ACTION")
          |> filter(fn: (r)=> r["_value"] == "workingArea" or r["_value"] == "IN")
          |> count()
        `;

        try {
          const response = await axios.post(
            `${url}/api/v2/query`,
            { query },
            {
              params,
              headers: {
                Authorization: `Token ${token}`,
                "Content-Type": "application/json",
                Accept: "application/csv",
              },
              responseType: "text", // CSV 데이터를 텍스트로 받음
            }
          );

          // CSV 데이터를 파싱하여 JSON 객체로 변환
          const parsedData = Papa.parse(response.data, { header: true });


          let value = "";
          // 가져온 데이터가 없을 경우 빈 값을 저장
          try {
            value = parsedData.data.length > 0 ? parsedData.data[1]._value : 0;
          } catch (error) {
            value = "0";
          }

          const period = `${hour}:00 ~ ${hour + intervalHours}:00`;

          extractedData.push({
            period,
            value,
          });
        } catch (error) {
          console.error("error =", error);
        }
      }

      // valuesArray 생성
      const valuesArray = extractedData.map((item) => parseInt(item.value));
      

      return valuesArray;
    },

