    generateRandomData(startTime, numPeriods) {
      // zoneA , B, C 에 넣으면 된다
      const data = {
        periods: [],
        zoneA: [],
        zoneB: [],
        zoneC: [],
      };

      this.workingAreaData().then((result) => {
        console.log("working =", result);
        data.zoneA = result;

        // workingAreaData의 비동기 작업이 완료된 후에 generateRandomData 함수 실행
        this.continueGenerateRandomData(startTime, numPeriods, data);
      });

      this.safeAreaData().then((result) => {
        console.log("safeAreaaData =", result);
        data.zoneB = result;

        this.continueGenerateRandomData(startTime, numPeriods, data);
      });

      this.restrectedAreaData().then((result) => {
        console.log("restrectedAreaData =", result);
        data.zoneC = result;

        this.continueGenerateRandomData(startTime, numPeriods, data);
      });
    },

    continueGenerateRandomData(startTime, numPeriods, data) {
      for (let i = 0; i < numPeriods; i++) {
        const currentTime = new Date(
          startTime.getTime() + 2 * 60 * 60 * 1000 * i
        ); // 2시간 간격으로 증가
        data.periods.push(currentTime.getHours() + ":00");
      }

      this.drawCustomChart01(data);
      this.drawCustomChart02(data);
    },

    drawCustomChart01(data) {
      const chartWrapper = document.getElementById("chartWrapper1");
      const canvas = chartWrapper.querySelector("canvas");
      const ctx = canvas.getContext("2d");

      if (this.chart1) {
        this.chart1.destroy();
      }

      const zoneALabel = "Working";
      const zoneBLabel = "Safe";
      const zoneCLabel = "Restrected";

      const zoneAData = data.zoneA;
      const zoneBData = data.zoneB;
      const zoneCData = data.zoneC;

      const periods = [
        "08:00",
        "10:00",
        "12:00",
        "14:00",
        "16:00",
        "18:00",
        "20:00",
      ];

      const periodLabels = [];

      for (let i = 0; i < periods.length; i++) {
        const startTimeParts = periods[i].split(":");
        const startHours = parseInt(startTimeParts[0]);

        const endTimeParts = periods[(i + 1) % periods.length].split(":");
        const endHours = parseInt(endTimeParts[0]);

        // "8pm ~ 8am"와 같은 구간은 제외
        if (startHours >= endHours) {
          continue;
        }

        const formattedStartTime =
          startHours >= 12
            ? startHours === 12
              ? "12pm"
              : startHours - 12 + "pm"
            : startHours + "am";
        const formattedEndTime =
          endHours >= 12
            ? endHours === 12
              ? "12pm"
              : endHours - 12 + "pm"
            : endHours + "am";

        const resultTime = formattedStartTime + " ~ " + formattedEndTime;
        periodLabels.push(resultTime);
      }

      const backgroundColors = ["#416fd6", "#79b77d", "#ebb5b4"];

      const borderColors = ["#416fd6", "#79b77d", "#ebb5b4"];

      canvas.width = chartWrapper.clientWidth;
      canvas.height = chartWrapper.clientHeight;

      this.chart1 = new Chart(ctx, {
        type: "bar",
        data: {
          labels: periodLabels, // 수정된 부분
          datasets: [
            {
              label: zoneALabel,
              data: zoneAData,
              backgroundColor: backgroundColors[0],
              borderColor: borderColors[0],
              borderWidth: 1,
              barThickness: 12,
            },
            {
              label: zoneBLabel,
              data: zoneBData,
              backgroundColor: backgroundColors[1],
              borderColor: borderColors[1],
              borderWidth: 1,
              barThickness: 12,
            },
            {
              label: zoneCLabel,
              data: zoneCData,
              backgroundColor: backgroundColors[2],
              borderColor: borderColors[2],
              borderWidth: 1,
              barThickness: 12,
            },
          ],
        },
        options: {
          scales: {
            yAxes: [
              {
                gridLines: {
                  color: "#cccccc", // y축 그리드 라인 색상 설정
                },
                ticks: {
                  fontColor: "#cecece", // y축 눈금 라벨 색상 설정
                },
              },
            ],
            xAxes: [
              {
                gridLines: {
                  color: "#222734", // x축 그리드 라인 색상 설정
                },
                ticks: {
                  fontColor: "#cecece", // x축 눈금 라벨 색상 설정
                },
                // barPercentage: 3, // 막대 묶음 간격 설정
                // barThickness: 10,   // 막대 폭 설정
                // categoryPercentage: 5, // 묶음 내 막대 간격 설정
              },
            ],
          },
          plugins: {
            datalabels: {
              anchor: "end",
              align: "top",
              font: {
                weight: "bold",
              },
            },
          },
          legend: {
            position: "right", // 레전드를 오른쪽에 배치
            labels: {
              fontWeight: "bolder",
              boxWidth: 8,
              fontSize: 16,
            },
          },
        },
      });
    },

    drawCustomChart02(data) {
