        const endHours = parseInt(endTimeParts[0]);

        // "8pm ~ 8am"와 같은 구간은 제외
        if (startHours >= endHours) {
          continue;
        }

        const formattedStartTime =
          startHours >= 12
            ? startHours === 12
              ? "12pm"
              : startHours - 12 + "pm"
            : startHours + "am";
        const formattedEndTime =
          endHours >= 12
            ? endHours === 12
              ? "12pm"
              : endHours - 12 + "pm"
            : endHours + "am";

