            },
            {
              label: zoneBLabel,
              data: zoneBData,
              backgroundColor: "transparent", // 채우기 없음
              borderColor: borderColors[1],
              borderWidth: 3,
              lineTension: 0,
            },
            {
              label: zoneCLabel,
              data: zoneCData,
              backgroundColor: "transparent", // 채우기 없음
              borderColor: borderColors[2],
