              lineTension: 0,
            },
          ],
        },
        options: {
          scales: {
            yAxes: [
              {
                gridLines: {
                  color: "#cccccc", // y축 그리드 라인 색상 설정
                },
                ticks: {
                  fontColor: "#cecece", // y축 눈금 라벨 색상 설정
                },
              },
            ],
            xAxes: [
              {
                gridLines: {
                  color: "#222734", // x축 그리드 라인 색상 설정
                },
                ticks: {
                  fontColor: "#cecece", // x축 눈금 라벨 색상 설정
                },
              },
            ],
          },
          plugins: {
            datalabels: {
              anchor: "end",
              align: "top",
              font: {
                weight: "bold",
              },
            },
          },
          legend: {
            position: "right", // 레전드를 오른쪽에 배치
            labels: {
              fontWeight: "bolder",
              boxWidth: 8,
              fontSize: 16,

              // 사각형 안에 색상
              generateLabels: function (chart) {
                const data = chart.data;
                if (data.datasets.length) {
                  return data.datasets.map((dataset, i) => ({
                    text: dataset.label,
                    fillStyle: backgroundColors[i], // 배경색 지정
                  }));
                }
              },
            },
          },
        },
      });
    },

    CSVDateList() {
      const currentDate = new Date();
      const dateOptions = { year: "numeric", month: "2-digit", day: "2-digit" };

      for (let i = 0; i < 15; i++) {
        const previousDate = new Date();
        //previousDate.setDate(currentDate.getDate() - i); // 오늘부터
        previousDate.setDate(currentDate.getDate() - (i + 1)); //어제부터
        const formattedDate = previousDate.toLocaleDateString(
          "en-US",
          dateOptions
        );
        this.fileList.push(`${formattedDate} 검사결과.excel`);
      }
    },

    async CSVData() {
      const token =
        "cnmJjxQvDJBOAHl5Q6UW_GlBQmMRTD5baVAOyoZb67mpLqMtG5chA94KimPPtTnqiVOZk0LGin-fkJuktTThbQ==";
      const url = "http://211.55.84.163:7984";

      const currentDate = new Date();
      currentDate.setDate(currentDate.getDate() - 1); // 어제 날짜로 설정

      const year = currentDate.getFullYear();
      const month = (currentDate.getMonth() + 1).toString().padStart(2, "0");
      const day = currentDate.getDate().toString().padStart(2, "0");
      const formattedDate = `${year}-${month}-${day}`;
      console.log(formattedDate);

      const params = { org: "paimedialab" };

      const startManualDate = `${formattedDate}T00:00:00Z`; // 어제 날짜의 시작
      const endManualDate = `${formattedDate}T23:59:59Z`; // 어제 날짜의 끝

      const query = `
      from(bucket: "localization")
      |> range(start: ${startManualDate}, stop: ${endManualDate})
      |> filter(fn: (r) => r["Ort"] == "IFA")
      |> count()
    `;

      try {
        const response = await axios.post(
          `${url}/api/v2/query`,
          { query },
          {
            params,
            headers: {
              Authorization: `Token ${token}`,
              "Content-Type": "application/json",
              Accept: "application/csv",
            },
          }
        );

        // CSV 데이터를 파싱하여 JSON 객체로 변환
        const parsedData = response.data;

        //console.log("parsedData = ", parsedData)

        return parsedData; // 전체 데이터를 반환
      } catch (error) {
        console.error("error =", error);
        return []; // 에러 발생 시 빈 배열 반환
      }
    },

    async downloadCSV() {
      const self = this;

      async function fetchDataAndDownload() {
        try {
          const rawData = await self.CSVData();
          //console.log("Raw Data = ", rawData);

          // 데이터를 CSV 파일로 다운로드
          self.downloadCSVFile(rawData);
        } catch (error) {
          console.error("Error fetching data:", error);
        }
      }

      fetchDataAndDownload();
      //console.log("selectDate = ",this.selectDate)
    },

    downloadCSVFile(data) {
      // CSV 파일 이름 지정
      const fileName = "data.csv";

      // CSV 파일 다운로드 링크 생성
      const downloadLink = document.createElement("a");
      downloadLink.href = URL.createObjectURL(
        new Blob([data], { type: "text/csv" })
      );
      downloadLink.download = fileName;

      // 다운로드 링크 클릭 및 제거
      downloadLink.click();
      URL.revokeObjectURL(downloadLink.href);
    },
    
    SectionDateList() {
      const currentDate = new Date();
      const dateOptions = { year: "numeric", month: "2-digit", day: "2-digit" };

      for (let i = 0; i < 5; i++) {
        const previousDate = new Date();
        previousDate.setDate(currentDate.getDate() - i); // 오늘부터
        // previousDate.setDate(currentDate.getDate() - (i + 1)); //어제부터
        const formattedDate = previousDate.toLocaleDateString(
          "en-US",
          dateOptions
        );
        this.sectionList.push(`${formattedDate}`);
      }
      console.log("sectionList = ",this.sectionList)
    },

    convertToDateTimeFormat: () =>  {
      const currentDate = new Date();
      const dateOptions = { year: "numeric", month: "2-digit", day: "2-digit" };

      for (let i = 0; i < 5; i++) {
        const previousDate = new Date();
        previousDate.setDate(currentDate.getDate() - i); // 오늘부터
        //previousDate.setDate(currentDate.getDate() - (i + 1)); //어제부터
        const formattedDate = previousDate.toLocaleDateString(
          "en-US",
          dateOptions
        );
        this.dates.push(formattedDate);
      }
      console.log("section = ",this.dates)
    },
  },



};
</script>

