import Vue from 'vue'
import Vuex from 'vuex'


import {
  AreaDataInfo,
  returnSectionInfo,
  postSectionInfo,
  returnSingleCam,
  returnAreaInfo,
  getHeatmapImage
} from '@/api/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    calibrationData :[],
    areaData : [],
    sectionInfo : {},

    useEmail:"false",
    useSpeaker:"false",
    useTelBot:"false",
    detectionType : "p",
    mapImagePath : "/media/testmap.png",
    email : "...",
    alarmMessage : "..",
    startTimeValue: {
      HH: "01",
      mm: "02",
    },

    endTimeValue: {
      HH: "03",
      mm: "04",
    },
    camID : 1,
    camInfos : [[],[],[],[],[]],
    camCircles : [[],[],[],[],[]],
    selectedCamera: { name: "Cam1", value: "1OFFICE1.jpg" },
    imageUrl : "http://192.168.33.19/media/1OFFICE1.jpg",
    AreaInfo : [],
    AreaRect : [],
    heatmapImage : null,
  },

  getters: {
    GET_RECTS: (state) =>{
      return state.AreaRect;
    },

    GET_CIRCLES: (state) =>{
      return state.camCircles;
    },

    GET_LINES: (state) =>{
      return state.camInfos;
    },

    GET_IMAGE: (state) =>{
      return state.imageUrl;
    },

    GET_SELECTEC_CAM: (state) =>{
      return state.selectedCamera;
    },

    GET_AREA: (state) =>{
      return state.areaData;
    },

    GET_SECTION_INFO: (state) => {
      return state.sectionInfo;
    },

    GET_USE_EMAIL: (state) => {
      return state.useEmail;
    },
    GET_USE_TELBOT: (state) => {
      return state.useTelBot;
    },
    GET_USE_SPECKER: (state) => {
      return state.useSpeaker;
    },

    GET_EMAIL_ADDRESS: (state) => {
      return state.email;
    },

    GET_ALARM_MESSAGE: (state) => {
      return state.alarmMessage;
    },

    GET_ST: (state) => {
      return state.startTimeValue;
    },

    GET_ET: (state) => {
      return state.endTimeValue;
    },

    GET_CAMID: (state) => {
      return state.camID;
    },

    GET_ITEMS: (state) => {
      return state.AreaInfo;
    },



  },

  mutations: {
    //*
    SET_RECTS: (state,payload) =>{
      state.AreaRect = payload;
    },

    SET_ITEMS: (state,payload) =>{
      state.AreaInfo = payload;
    },
    //*
    SET_LINES: (state,payload) =>{
      state.camInfos[payload.index] = payload.line;
    },
    SET_CIRCLES: (state,payload) =>{
      state.camCircles[payload.index] = payload.circle;
    },

    SET_IMAGE: (state, payload) =>{
      state.imageUrl = payload;
    },
    SET_SELECTEC_CAM:(state, payload) =>{
      state.selectedCamera = payload;
    },

    SET_AREA_DATA:(state, payload) =>{
      state.areaData = payload;
    },

    SET_START_TIME:(state, payload) =>{
      state.startTimeValue = payload;
    },

    SET_END_TIME:(state, payload) =>{
      state.endTimeValue = payload;
    },

    SET_END_TIME:(state, payload) =>{
      state.endTimeValue = payload;
    },
    SET_USE_EMAIL:(state, payload) =>{
      state.useEmail = payload;
    },
    SET_USE_TELBOT:(state, payload) =>{
      state.useTelBot = payload;
    },
    SET_USE_SPECKER:(state, payload) =>{
      state.useSpeaker = payload;
    },
    SET_MESSAGE:(state, payload) =>{
      state.alarmMessage = payload;
    },

    SET_SINGLE_CAM:(state, payload) =>{
      var camid = payload.response.camId
      
      state.camInfos[camid] =
      [
        payload.response["pickle_point1"][0] *100,
        payload.response["pickle_point1"][1] *100,
        payload.response["pickle_point2"][0] *100,
        payload.response["pickle_point2"][1] *100,
        payload.response["pickle_point1"][0] *100,
        payload.response["pickle_point1"][1] *100,
        payload.response["pickle_point3"][0] *100,
        payload.response["pickle_point3"][1] *100,
        payload.response["pickle_point4"][0] *100,
        payload.response["pickle_point4"][1] *100,
        payload.response["pickle_point2"][0] *100,
        payload.response["pickle_point2"][1] *100,
      ]

      state.camCircles[camid] = [
        {
          x:payload.response["pickle_point1"][0] *100,
          y:payload.response["pickle_point1"][1] *100
        },
        {
          x:payload.response["pickle_point2"][0] *100,
          y:payload.response["pickle_point2"][1] *100
        },
        {
          x:payload.response["pickle_point3"][0] *100,
          y:payload.response["pickle_point3"][1] *100
        },
        {
          x:payload.response["pickle_point4"][0] *100,
          y:payload.response["pickle_point4"][1] *100
        },

      ]
      console.log("camID:", state.camInfos)
    },

    SET_CAMID:(state, payload) =>{
      state.camID = payload
    },

    SET_SECTION_INFO:(state, payload) =>{
      console.log("payload:" ,payload.response)
      state.useEmail = payload.response.useEmail.toString();
      state.useSpeaker = payload.response.useSpeaker.toString();
      state.useTelBot = payload.response.useTelBot.toString();

      state.detectionType = payload.response.detectionType;
      state.email = payload.response.email;
      state.alarmMessage = payload.response.alarmMessage;

      state.mapImagePath = payload.response.mapImagePath;
      
      state.startTimeValue.HH = payload.response.monitoringScheduleStartTime.split(':')[0];
      state.startTimeValue.mm = payload.response.monitoringScheduleStartTime.split(':')[1];
      state.endTimeValue.HH = payload.response.monitoringScheduleEndTime.split(':')[0];
      state.endTimeValue.mm = payload.response.monitoringScheduleEndTime.split(':')[1];

      console.log("Time Set:", state.startTimeValue, state.endTimeValue)
      // state.sectionInfo = payload
    },
    SET_AREA_INFO:(state, payload) =>{
      console.log("AreaInfo:",payload)
      // state.AreaInfo = payload.response;
      state.AreaRect = []
      state.AreaInfo = []
      payload.response.forEach(element => {
        state.AreaRect.push({
          areaName: element.areaName,
          startPointX: (element.area_x1)* 0.62,
          startPointY: (element.area_y1) * 0.84,
          width: (element.area_x2 - element.area_x1) * 0.62 ,
          height: (element.area_y2 - element.area_y1) * 0.84,
          color: "#"+element.areaColor,
          menu : false,
          menu2 : false
        })

      state.AreaInfo.push({
        areaName: element.areaName,
        startPointX: (element.area_x1)* 0.62,
        startPointY: (element.area_y1) * 0.84,
        width: (element.area_x2 - element.area_x1) * 0.62 ,
        height: (element.area_y2 - element.area_y1) * 0.84,
        color: "#"+element.areaColor,
        menu : false,
        menu2 : false
      })
      });
      console.log("state.AreaRect:", state.AreaRect)
    },
  
    SET_CAMERA_INFO:(state, payload) =>{
      state.alarmMessage = payload;
    },
    SET_HEATMAP:(state, payload) =>{
      state.heatmapImage = payload;
    },
  },
  actions: {
    async GETAreaDataInfo(context){
      const response = await AreaDataInfo();
      context.commit('SET_AREA_DATA', response.data)
      return response;
    },

    async FETCH_SECTION_INFO(context){
      const response = await returnSectionInfo();
      console.log(response);
      context.commit('SET_SECTION_INFO', response.data)
    },
    async FETCH_AREA_INFO(context){
      const response = await returnAreaInfo();
      console.log(response);
      context.commit('SET_AREA_INFO', response.data)
    },

    async FETCH_SINGLE_CAM(context, camID){
      const response = await returnSingleCam(camID);
      console.log("CAM INFO:",response);
      context.commit('SET_SINGLE_CAM', response.data)
    },

    async FETCH_HEATMAP(context){
      const response = await getHeatmapImage();
      context.commit('SET_SINGLE_CAM', response.data)
    },

    async POST_SECTION_INFO(context, payload){    
    // console.log("add sum:", this.state.startTimeValue.HH + this.state.startTimeValue.mm)
     var defineProps = {
        "section_name" : "IFA",
        "monitoring_start_time" : this.state.startTimeValue.HH + this.state.startTimeValue.mm,
        "monitoring_end_time" : this.state.endTimeValue.HH + this.state.endTimeValue.mm ,
        "alarm_message" : this.state.alarmMessage,
        "detect_object_type" : "P",
        "use_speaker_flag": this.state.useSpeaker,
        "use_email_flag": this.state.useEmail,
        "use_telbot_flag": this.state.useTelBot
      }

      const response = await postSectionInfo(defineProps);

      // context.commit('SET_SECTION_INFO', response.data)
    }

  },
  modules: {
  }
})
