<template>
  <v-container class="mt-5" style="background-color: #2f3546" fill-height>
    <v-layout row wrap>
      <v-card
        tile
        class="ma-4 pa-4"
        style="background-color: #000000"
        height="620"
        width="1100"
        align="center"
        justify="center"
      >
        <template>
          <div class="ct mt-4">
            <v-stage
              ref="stage"
              :config="stageSize"
              @mousedown="handleMouseDown"
              @mouseup="handleMouseUp"
            >
              <v-layer ref="layer">
                <v-image
                  :config="{
                    x: 0,
                    y: 0,
                    width: stageSize.width,
                    height: stageSize.height,
                    image: image,
                  }"
                />

                <v-circle
                  v-for="cir in circles"
                  :key="cir.id"
                  :config="{
                    x: cir.x,
                    y: cir.y,
                    radius: 10,
                    fill: 'red',
                  }"
                />

                <v-line
                  :config="{
                    x: 0,
                    y: 0,
                    points: lines,
                    stroke: 'black',
                  }"
                />

                <!-- <v-line
                  v-for="line in lines"
                  :key="line.id"
                  :config="{
                    stroke: 'black',
                    points: line.points,
                  }"
                /> -->
              </v-layer>
            </v-stage>
          </div>

        </template>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import constants from "@/constants";

const width = window.innerWidth;
const height = window.innerHeight;

export default {
  name: "Calibration",

  data() {
    return {
      stageSize: {
        width: 960,
        height: 540,
      },
      image: null,
      lines: [],
      circles : [],
      cameraID : "",
      cameraImg : "",
      image_src : "",
    };
  },

  props : ['selectedCamera'],
  created() {
  },

  mounted() {
    const container = document.querySelector(".ct");
    const observer = new ResizeObserver(() => {
      this.stageSize.width = container.offsetWidth;
      this.stageSize.height = container.offsetHeight;
    });
    observer.observe(container);
    const image = new window.Image();
    // image.src = "http://192.168.33.19/media/" + this.selectedCamera.value
    image.src = this.check_image
    // this.image_src = "http://192.168.33.19/media/" + this.selectedCamera.value
    image.onload = () => {
      this.image = image;
    };
    
    this.getCamInfo(0)
    
  },

  computed : {
    check_image:{
      get(){
       return this.$store.getters.GET_IMAGE
       },
      set(val){
        console.log("check_image val:",val)
        this.$store.commit("SET_IMAGE", val);
      }
      },

    // check_line:{
    //   get(){
    //     var cam_id = this.$store.getters.GET_CAMID
    //     var temp_line = this.$store.getters.GET_LINES
    //     return temp_line[cam_id]
    //     // return this.$store.getters.GET_LINES
    //    },
    //   set(val){
    //     console.log("checkLine set : ",val)
    //     this.$store.commit("SET_LINES", val);
    //   }
    //   },

    // check_circles:{
    //   get(){
    //     return this.$store.getters.GET_CIRCLES
    //    },
    //   set(val){
    //     console.log(val)
    //     this.$store.commit("SET_IMAGE", val);
    //   }
    //   },


  },

  watch: {
    check_image(val){
      this.image_src = val
    },
    image : function(e) {
      console.log(this.image)
    }
  },

  updated() {
    //image.src = this.check_image
    // image.onload = () => {
    //   this.image = image;
    // };

    
    this.image.onload = () => {
    };
    this.setImageurl()
    
    // this.image.src = "http://192.168.33.19/media/" + this.selectedCamera.value
  },


  methods: {
    ...mapActions([
      "FETCH_SINGLE_CAM",
    ]),

    getCamInfo(id){
      console.log("NANss::", id)
      // this.$store.dispatch("FETCH_SINGLE_CAM", id);
      // this.lines = this.$store.camInfos[id]
      var lines = this.$store.getters.GET_LINES
      var circle = this.$store.getters.GET_CIRCLES
      this.lines = lines[id]
      this.circles = circle[id]
      this.cameraID = id
    },

    setImageurl(){
      console.log("image sett!", this.selectedCamera.value)
      this.image.src = "http://192.168.33.19/media/" + this.selectedCamera.value
    },

    handleMouseUp() {
      // this.isDrawing = false;
    },

    handleMouseDown(e) {
      this.isDrawing = true;
      const pos = e.target.getStage().getPointerPosition();
      console.log("lineLength : " ,this.lines.length)
      if (this.lines.length <= 8) {

        if (this.lines.length == 4){
          this.lines.push(this.lines[0]);
          this.lines.push(this.lines[1]);
        }
        this.lines.push(pos.x);
        this.lines.push(pos.y);
        this.circles.push({x:pos.x, y:pos.y})

        if (this.lines.length == 10){
          this.lines.push(this.lines[2]);
          this.lines.push(this.lines[3]);
        }
      }

      console.log(this.lines);
    },

    clearPoints() {
      this.lines = [];
      this.circles = [];
    },

    local_confirm(){
      console.log("cam ID", this.cameraID)
      console.log("lines:", this.lines)
      this.$store.commit("SET_LINES", {index: this.cameraID ,line:this.lines});
      this.$store.commit("SET_CIRCLES", {index: this.cameraID ,circle:this.circles});
    }
  },
};
</script>
<style>
